body {
  background-image: url("./img/bg.jpg");
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: white;
}

.title {
  text-align: center;
  padding: 60px 0;
  background-color: rgba(0, 0, 0, 0.8);
  margin: 0 0 25px 0;
}

.block {
  position: relative;
  box-sizing: border-box;
  margin-bottom: 90px;
  overflow: hidden;
  text-align: center;
  color: #fff;
  border-radius: 5px;
  transition: rotate 0.5s ease-in-out;
  padding: 10px;
}

.block:nth-child(odd):hover {
  transform: rotate(-2deg);
}

.block:nth-child(even):hover {
  transform: rotate(2deg);
}

.blocks-wrapper {
  padding: 50px;
}

.block__date {
  font-family: "Poppins", cursive;
  display: inline-block;
  padding: 15px 27px;
  font-size: 2rem;
  font-weight: bold;
  background: rgba(0, 0, 0, 0.8);
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  text-shadow: 1px 2px 11px #000;
}

.block__winner {
  position: relative;
  top: -2px;
  font-family: "Poppins", cursive;
  display: inline-block;
  padding: 15px 27px;
  font-size: 1.5rem;
  font-weight: bold;
  background: rgba(0, 0, 0, 0.8);
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  text-shadow: 1px 2px 11px #000;
}

.block__photo {
  position: relative;
  border-radius: 5px;
  min-height: 350px;
  max-width: 550px;
  margin: 0 auto;
}

.block__photo .photo {
  border-radius: 5px;
  box-shadow: 0 0px 12px 5px rgba(0, 0, 0, 0.5);
  width: 550px;
  height: 350px;
}

.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out,
    box-shadow 0.15s ease-in-out;
}

.btn:hover {
  cursor: pointer;
}

.terms {
  color: white;
  text-decoration: underline;
  cursor: pointer;
}

.row.footer {
  flex-direction: column;
  align-content: center;
  text-align: center;
}

.list {
  text-align: left;
}

.list li {
  margin-bottom: 10px;
}

.decoration {
  position: absolute;
  width: 90px;
  left: -20px;
}

.hat {
  top: -26px;
  transform: rotate(-7deg);
}

.balls {
  top: -27px;
  transform: rotate(-7deg);
}

.bell {
  top: -20px;
}

.block:nth-child(even) .hat {
  top: -31px;
}

.block:nth-child(even) .hat,
.block:nth-child(even) .bell,
.block:nth-child(even) .balls {
  right: -20px;
  left: auto;
  transform: scaleX(-1);
}

.tree {
  position: absolute;
  bottom: 1px;
  right: -25px;
  height: 120px;
}

.tw-button {
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  align-items: center;
  display: inline-flex;
  font-size: 1.2rem;
  justify-content: center;
  line-height: 2rem;
  outline: none;
  position: relative;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  background: #6441a4;
  border: 1px solid transparent;
  border-radius: 4px;
  color: #fff;
  margin-bottom: 20px;
}

.tw-button:hover {
  background: #7d5bbe;
  border-color: #7d5bbe;
  color: #fff;
}

@media only screen and (max-width: 1050px) {
  .blocks-wrapper {
    padding: 0;
  }
  .block__photo {
    max-width: 100%;
    min-height: 100%;
  }
  .title img {
    width: 90%;
  }
  .tree {
    display: none;
  }
  .block__photo .photo {
    width: 100%;
    height: 100%;
  }
  .ReactModal__Content {
    width: auto !important;
    height: auto !important;
  }
}
